
html, body {
  height: 100%;
  background-color: #1f2329;
  color: #ffffff;
}
.navLink {
  color: #ffffff;
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
  padding: 10px;
}
.navLink:hover {
  color: #ffffff;
}
.container-fluid, .wrapper {
  height: 100%;
}
.content-area, .article-tree{
  overflow:auto;
  height: 100%;
}
.footer-copyright {
  color: gray;
}


.lineChart {
  background: black;
}

#map {
  width: 100%;
  height: 500px;
}
.covid-case {
  position: absolute;
}
.covid-case svg {
  width: 500px;
  height: 500px;
  font: 10px sans-serif;
  position: absolute;
  pointer-events: none;
}
.marker-circle {
  pointer-events: all;
}
.tooltip {
  font-size: 1em;
  position: absolute;
  top: 0px;
  left: 0px;
  padding: 10px 20px;
  border-radius: 5px;
  background: #1f8ac0;
  color: white;
}


.svg-container {
    display: inline-block;
    position: relative;
    width: 100%;
    padding-bottom: 10%; /* aspect ratio */
    vertical-align: top;
    overflow: hidden;
}
.svg-content-responsive {
    display: inline-block;
    position: absolute;
    top: 10px;
    left: 0;
}
.ticks {
  font-size: 10px;
}
.ticks > text {
  fill: #dcdcdc;
}
.track,
.track-inset,
.track-overlay {
  stroke-linecap: round;
}
.track {
  stroke: #000;
  stroke-opacity: 0.3;
  stroke-width: 10px;
}
.track-inset {
  stroke: #dcdcdc;
  stroke-width: 8px;
}
.track-overlay {
  pointer-events: stroke;
  stroke-width: 50px;
  stroke: transparent;
  cursor: crosshair;
}
.handle {
  fill: #fff;
  stroke: #000;
  stroke-opacity: 0.5;
  stroke-width: 1.25px;
}

.node circle {
  stroke: #3a403d;
  stroke-width: .5px;
}
#my_dataviz {
  text-align: center;
  position: relative;
}
.node:hover{
  stroke-width: 7px;
  opacity: 1;
}
.tooltip {
  font-size: 1em;
  position: absolute;
  top: 20px;
  left: 20px;
  padding: 10px 20px;
  border-radius: 5px;
  background: #1f8ac0;
  color: white;
  width: 600px;
}



.application {
  font-family: 'Raleway', sans-serif;
  line-height: 2rem;
  text-align: center;
}
.tooltip {
  font-size: 1em;
  position: absolute;
  top: 0px;
  left: 0px;
  padding: 10px 20px;
  border-radius: 5px;
  background: #1f8ac0;
  color: white;
}
.axis path {
  stroke: white;
}
.axis line {
  stroke: white;
}
.axis text {
  fill: white;
}

.pointer {cursor: pointer;
}
hr {
  border-top: 1px solid rgba(255,255,255,1);
}













































